






import Vue from "vue";

import { Deck } from "@/lib/entity/deck";

export default Vue.extend({
  name: "DeckPreview",
  methods: {
    createLesson: function(deck: Deck) {
      this.$store.state.currentLesson = deck.createLesson();
      this.$router.push("/lesson");
    }
  },
  props: {
    deck: Deck
  }
});
